/* Colores */
$negro: #262626;
$blanco: #FFF;
$naranja: #e99401;

$primary: $naranja;
$white: $blanco;
$black: $negro;
$dark: $negro;

/* Tipografia */
$font-size-root: 62.5%;
$font-size-base: 1.6rem;

$fuente-heading: 'Outfit', sans-serif;
$fuente-textos: 'Outfit', sans-serif;

$font-family-base: $fuente-textos;
$headings-font-family: $fuente-heading;
$headings-font-weight: 900;
$h1-font-size: 4.4rem;
$h1-font-size: 3.6rem;
$h2-font-size: 2.6rem;
$headings-color: $primary; 

.fw-black {
  font-weight: 900; 
}

/* Botones */
$btn-font-weight: 900;
$btn-border-radius: 0;

/* Links */
$link-color: $white;
$link-hover-color: $white;
$link-decoration: none;

/* Espaciado */
$spacer: 1rem;
$spacers: (
  0: 0,
  1: $spacer,
  2: $spacer * 2,
  3: $spacer * 3,
  4: $spacer * 4,
  5: $spacer * 5,
);

/* Cards */
$card-border-radius: 0;
// $card-border-width: 0;


// Importar Bootstrap
@import '../../node_modules/bootstrap/scss/bootstrap.scss';